<template>

  <div style="height: 100%;">
    
  </div>

</template>

<script>
import eventBus from '../global-events';

export default {
  props: ['client'],
  data() {
    return {
    }
  },

  watch: {
    'client': function(client, oldClient) {
      if (oldClient) {
        this.detatchClient(oldClient);
      }

      if (client){
        this.attachClient(client);
      }
    }
  },

  created() {
  	// this.client.connect();
  },

  mounted() {
  	if (this.client)
      this.attachView();
  },

  updated() {
    if (this.client) {
    	let view = this.client._renderer.view;
    	if (view.parentElement !== this.$el) {
    		view.parentElement.removeChild(view);
  	  	this.attachView();
    	}
    }
  },

  beforeDestroy() {
    if (this.client)
      this.detatchClient(this.client);
  },

  methods: {
  	attachView() {
	  	this.$el.appendChild(this.client._renderer.view);
	  	this.resizeView();
  	},

  	resizeView() {
	  	this.client._renderer.resize(this.$el.offsetWidth, this.$el.offsetHeight);
  	},

    attachClient(client) {
      eventBus.$on('resize', this.resizeView);
      client.connect();
      this.attachView();
    },

    detatchClient(client) {
      let view = client._renderer.view;
      view.parentElement.removeChild(view);
      client._renderer.destroy();
      client.disconnect();
      eventBus.$off('resize', this.resizeView);
    }
  }
}
</script>

<style>

</style>



